import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
}));



const SimpleSelect = ({subjectName}) => {
  const classes = useStyles();
  const [grade, setGrade] = React.useState('');

  const handleChange = (event) => {
    setGrade(event.target.value);
  };

  const possibleGrades = [10,9,8,7,6,5,4,'S'];
  const all = possibleGrades.map((grade) => <MenuItem value={grade}>{grade}</MenuItem>);   

  return (
    <div>
      <FormControl className={classes.formControl}>
        <InputLabel id="demo-simple-select-helper-label">{subjectName}</InputLabel>
        <Select
          labelId="demo-simple-select-helper-label"
          id="demo-simple-select-helper"
          value={grade}
          onChange={handleChange}
        >
          {all} 
        </Select>
      </FormControl>
    </div>
  );
}

export default SimpleSelect
